import React, { useState, useEffect } from "react";
import { useLocation } from "react-router-dom";
import { Container, Row, Col } from "react-bootstrap";
import headerImg from "../assets/images/header-img.svg";
import "animate.css";
import TrackVisibility from "react-on-screen";

export const Banner = () => {
  // 1. call hooks first:
  const { pathname } = useLocation();

  const [loopNum, setLoopNum]       = useState(0);
  const [isDeleting, setIsDeleting] = useState(false);
  const [text, setText]             = useState("");
  const [delta, setDelta]           = useState(300 - Math.random() * 100);
  const [index, setIndex]           = useState(1);

  const toRotate = [
    "Web Design",
    "UI/UX Design",
    "AI Engineer on the side"
  ];
  const period = 2000;

  useEffect(() => {
    const ticker = setInterval(() => {
      tick();
    }, delta);
    return () => clearInterval(ticker);
  }, [text, delta]); //  so timing stays correct

  const tick = () => {
    const i = loopNum % toRotate.length;
    const fullText = toRotate[i];
    const updatedText = isDeleting
      ? fullText.substring(0, text.length - 1)
      : fullText.substring(0, text.length + 1);

    setText(updatedText);

    if (isDeleting) {
      setDelta(prev => prev / 2);
    }

    if (!isDeleting && updatedText === fullText) {
      setIsDeleting(true);
      setIndex(prev => prev - 1);
      setDelta(period);
    } else if (isDeleting && updatedText === "") {
      setIsDeleting(false);
      setLoopNum(loopNum + 1);
      setIndex(1);
      setDelta(500);
    } else {
      setIndex(prev => prev + 1);
    }
  };

  // 2. Tskip rendering:
  if (pathname.startsWith("/design")) {
    return null;
  }

  return (
    <section className="banner" id="home">
      <Container>
        <Row className="aligh-items-center">
          <Col xs={12} md={6} xl={7}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__fadeIn" : ""}>
                  
                  <span className="tagline">Bienvenue sur mon portfolio</span>
                  
                  <h1>
                    {`Salut ! Je m'appelle Jonathan j'apprend le `}{" "}
                    <span
                      className="txt-rotate"
                      dataPeriod="1000"
                      data-rotate='[ "Web Developer", "Web Designer", "UI/UX Designer" ]'
                    >
                      <span className="wrap">{text}</span>
                    </span>
                  </h1>
                  
                  <p>
                    Je suis tudiant en Data Science  lUniversit dOttawa et je suis en train dapprendre le design dinterfaces utilisateur.
                    Ce portfolio est une vitrine de mon parcours dans ce cours. J'explore HTML, CSS, Bootstrap, et les bases de la
                    communication visuelle  travers plusieurs projets pratiques.
                  </p>

                  <p>
                    Vous trouverez ici quatre liens vers des projets de design  venir, chacun reprsentant un type diffrent de site :
                    services, jeux, commerce lectronique, et analyse/visualisation. Ces pages contiennent des descriptions
                    provisoires et seront enrichies au fil du semestre.
                  </p>

                </div>
              }
            </TrackVisibility>
          </Col>

          <Col xs={12} md={6} xl={5}>
            <TrackVisibility>
              {({ isVisible }) =>
                <div className={isVisible ? "animate__animated animate__zoomIn" : ""}>
                  <img src={headerImg} alt="Illustration den-tte" />
                </div>
              }
            </TrackVisibility>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

